<template>
  <v-card>
    <v-card-title>
      <v-spacer/>
    </v-card-title>
    <v-card-text>
      <v-data-table
        :headers="viewportAwareHeaders"
        :items="items"
        :total-items="$vuetify.breakpoint.mdAndUp ? items.length : undefined"
        id="main-table"
        hide-actions
      >
        <template slot="items" slot-scope="props">
          <tr v-bind:class="{ top: (props.index < 10) }">
            <td>{{ props.item.name }}</td>
            <td>{{ props.item.kelas }}</td>
            <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.rankKelas }}</td>
            <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.rataSikap }}</td>
            <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.rataPengetahuan }}</td>
            <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.rataTrampil }}</td>
            <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.rataSeluruh }}</td>
            <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.ekstra }}</td>
            <td class="text-xs-center">{{ parseFloat(props.item.rank).toFixed(4) }}</td>
          </tr>
        </template>
      </v-data-table>
    </v-card-text>
  </v-card>
</template>

<script>
import { Group } from '@/services'
import { mapGetters } from 'vuex'

export default {
  name: 'SmartView',
  data () {
    return {
      headers: [
        {
          text: 'Nama',
          value: 'name'
        },
        {
          text: 'KLS',
          value: 'kelas'
        },
        {
          text: 'RANK. KLS',
          value: 'rankKelas',
          align: 'center'
        },
        {
          text: 'R.SIKAP',
          value: 'rataSikap',
          align: 'center'
        },
        {
          text: 'R.PTH',
          value: 'rataPengetahuan',
          align: 'center'
        },
        {
          text: 'R.TRAMPIL',
          value: 'rataTrampil',
          align: 'center'
        },
        {
          text: 'R.SELURUH',
          value: 'rataSeluruh',
          align: 'center'
        },
        {
          text: 'EKS',
          value: 'ekstra',
          align: 'center'
        },
        {
          text: 'SMART',
          value: 'rank',
          align: 'center'
        }
      ],
      items: []
    }
  },
  computed: {
    ...mapGetters([
      'activeGroup'
    ]),
    viewportAwareHeaders () {
      if (this.$vuetify.breakpoint.smAndUp) {
        return this.headers
      }
      return this.headers.filter((v, i) => {
        return i === 0 || i === 1 || i === 8
      })
    }
  },
  methods: {
    async reload () {
      let items = await Group.smart(this.activeGroup.id)
      this.items = items
    }
  },
  async mounted () {
    this.reload()
  }
}
</script>

<style>
#main-table tr.top > td {
  font-weight: bold;
}
</style>
