import Vue from 'vue'
import Vuex from 'vuex'
import { Group } from '@/services'
import router from './router'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userId: 1,
    groups: [],
    members: [],
    membersSearch: '',
    activeGroupId: null,
    groupKeyword: '',
    dialogAddMember: false,
    dialogAddGroup: false,
    dialogEditGroup: false,
    dialogEditMember: false
  },
  mutations: {
    setGroups (state, groups) {
      state.groups = groups
    },
    setActiveGroupId (state, groupId) {
      state.activeGroupId = groupId
    },
    setGroupKeyword (state, keyword) {
      state.groupKeyword = keyword
    },
    setMembers (state, members) {
      state.members = members
    },
    setDialogAddMember (state, val) {
      state.dialogAddMember = val
    },
    setDialogAddGroup (state, val) {
      state.dialogAddGroup = val
    },
    setDialogEditGroup (state, val) {
      state.dialogEditGroup = val
    },
    setDialogEditMember (state, val) {
      state.dialogEditMember = val
    },
    setMembersSearch (state, val) {
      state.membersSearch = val
    }
  },
  getters: {
    activeGroup (state) {
      let x = state.groups.find(g => g.id === state.activeGroupId)
      if (!x) {
        return {
          id: null,
          name: '',
          ta: '',
          guru: ''
        }
      }
      return x
    },
    filteredGroups (state) {
      return state.groups.filter(g => g.name.includes(state.groupKeyword))
    },
    filteredMembers (state) {
      return state.members.filter(r => r.name.includes(state.membersSearch))
    }
  },
  actions: {
    async changeActiveGroupId ({ commit, state }, id) {
      // Get the members of this group
      let members = await Group.getMembers(id)
      commit('setMembers', members)
      console.log('Members', members)
      commit('setActiveGroupId', id)
      router.push('/app/groupdetail')
    },
    async addMember ({ commit, state }, siswa) {
      // Add the data
      await Group.addMember(state.activeGroupId, siswa)

      // Get the members of this group
      let members = await Group.getMembers(state.activeGroupId)
      commit('setMembers', members)

      // Hide dialog
      commit('setDialogAddMember', false)
    },
    async addGroup ({ commit, state }, group) {
      await Group.addGroup(state.userId, group)

      let groups = await Group.getGroups(state.userId)
      commit('setGroups', groups)
      commit('setDialogAddGroup', false)
    },
    async updateMember ({ commit, state }, siswa) {
      let { id, ...payload } = siswa
      await Group.updateMember(id, payload)
      // Get the members of this group
      let members = await Group.getMembers(state.activeGroupId)
      commit('setMembers', members)
      commit('setDialogEditMember', false)
    },
    async removeGroup ({ commit, state }, groupId) {
      await Group.removeGroup(groupId)
      let groups = await Group.getGroups(state.userId)
      commit('setGroups', groups)
    },
    async removeMember ({ commit, state }, id) {
      await Group.removeMember(id)
      let members = await Group.getMembers(state.activeGroupId)
      commit('setMembers', members)
    }
  }
})
