import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import SignUp from './views/SignUp.vue'

import GroupsPane from './components/GroupsPane.vue'
import MembersPane from './components/MembersPane.vue'
import RawView from './components/RawView'
import SmartView from './components/SmartView'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/signup',
      name: 'signup',
      component: SignUp
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    },
    {
      path: '/app',
      name: 'app',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/MainApp.vue'),
      children: [
        {
          path: 'groups',
          name: 'groups',
          component: GroupsPane
        },
        {
          path: 'groupdetail',
          name: 'groupDetail',
          component: MembersPane,
          redirect: '/app/groupdetail/raw',
          children: [
            {
              path: 'raw',
              component: RawView
            },
            {
              path: 'smart',
              component: SmartView
            }
          ]
        }
      ]
    }
  ]
})
