import { Axe } from './base'

export const Group = {

  async getGroups (userId) {
    let result = await Axe.get(`/user/group-siswa?userId=${userId}`).then(r => r.data)
    return result
  },

  async removeGroup (groupId) {
    await Axe.delete(`/user/group-siswa?groupId=${groupId}`).then(r => r.data)
  },

  async getMembers (groupId) {
    let result = await Axe.get(`/user/group-siswa/members?groupId=${groupId}`).then(r => r.data)
    return result
  },

  async addMember (groupId, siswa) {
    await Axe.post(`/user/group-siswa/members?groupId=${groupId}`, siswa).then(r => r.data)
  },

  async updateMember (id, siswa) {
    await Axe.put(`/user/group-siswa/members?id=${id}`, siswa).then(r => r.data)
  },

  async removeMember (id) {
    await Axe.delete(`/user/group-siswa/members?id=${id}`).then(r => r.data)
  },

  async addGroup (userId, group) {
    await Axe.post(`/user/group-siswa?userId=${userId}`, group).then(r => r.data)
  },

  async smart (groupId) {
    return Axe.get(`/smart?groupId=${groupId}`).then(r => r.data)
  },

  convertSiswaToArray (siswa) {
    return [
      siswa.name,
      siswa.kelas,
      siswa.rankKelas,
      siswa.matematika.pengetahuan,
      siswa.matematika.trampil,
      siswa.fisika.pengetahuan,
      siswa.fisika.trampil,
      siswa.kimia.pengetahuan,
      siswa.kimia.trampil,
      siswa.biologi.pengetahuan,
      siswa.biologi.trampil,
      siswa.sikap.sosial,
      siswa.sikap.spiritual,
      siswa.jumlah,
      siswa.ekstra
    ]
  }

}
