<template>
  <v-container>
    <v-layout>
      <v-flex xs12>
        <div class="title py-4 px-3">Daftar Group</div>
        <v-divider/>
        <v-list two-line>
          <template v-for="(g, i) in filteredGroups">
            <v-list-tile
              :key="i"
              v-bind:class="{ 'grey lighten-4': activeGroup.id == g.id }"
            >
              <v-list-tile-content>
                <v-list-tile-title>{{ g.name }}</v-list-tile-title>
                <v-list-tile-sub-title>{{ g.ta }}</v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-action>
                <v-layout row justify-space-between style="min-width: 100px">
                  <v-btn icon dark flat small color="green lighten-2"
                    @click="changeActiveGroupId(g.id)"
                  >
                    <font-awesome-icon icon="eye"></font-awesome-icon>
                  </v-btn>
                  <v-btn icon dark flat small color="blue lighten-2"
                    @click="openEditDialog(g)"
                  >
                    <font-awesome-icon icon="pencil-alt"></font-awesome-icon>
                  </v-btn>
                  <v-btn icon dark flat small color="red lighten-2" @click="removeGroup(g.id)">
                    <font-awesome-icon icon="trash"></font-awesome-icon>
                  </v-btn>
                </v-layout>
              </v-list-tile-action>
            </v-list-tile>
            <v-divider :key="'somedivi' + i"/>
          </template>
        </v-list>
      </v-flex>
    </v-layout>
    <v-dialog
      :value="dialogAddGroup"
      max-width="500"
      persistent
    >
      <v-card>
        <v-card-title>Tambah Group</v-card-title>
        <v-card-text>
          <v-text-field
            label="Nama Group"
            v-model="groupBaru.name"
          >
          </v-text-field>
          <v-text-field
            label="Tahun Ajaran"
            v-model="groupBaru.ta"
          >
          </v-text-field>
          <v-text-field
            label="Guru"
            v-model="groupBaru.guru"
          >
          </v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-btn @click="addGroup(groupBaru)" flat color="primary">OK</v-btn>
          <v-btn @click="setDialogAddGroup(false)" flat color="warning">Batal</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      :value="dialogEditGroup"
      persistent
      max-width="500"
    >
      <v-card>
        <v-card-title>
          Edit Data Group
        </v-card-title>
        <v-card-text>
          <v-text-field
            label="Nama Group"
            v-model="groupEdit.name"
          >
          </v-text-field>
          <v-text-field
            label="Tahun Ajaran"
            v-model="groupEdit.ta"
          >
          </v-text-field>
          <v-text-field
            label="Guru"
            v-model="groupEdit.guru"
          >
          </v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-btn color="blue" dark
          >
            Ok
          </v-btn>
          <v-btn color="red" dark
            @click="setDialogEditGroup(false)"
          >
            Batal
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { Group } from '@/services'
import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'

export default {
  data () {
    return {
      groupBaru: {
        name: '',
        ta: '',
        guru: ''
      },
      groupEdit: {
        id: null,
        name: '',
        ta: '',
        guru: ''
      }
    }
  },
  computed: {
    ...mapState({
      userId: state => state.userId,
      groups: state => state.groups,
      groupKeyword: state => state.groupKeyword,
      dialogAddGroup: state => state.dialogAddGroup,
      dialogEditGroup: state => state.dialogEditGroup
    }),
    ...mapGetters([
      'activeGroup',
      'filteredGroups'
    ])
  },
  methods: {
    ...mapMutations([
      'setGroups',
      'setGroupKeyword',
      'setDialogAddGroup',
      'setDialogEditGroup'
    ]),
    ...mapActions([
      'changeActiveGroupId',
      'addGroup',
      'removeGroup'
    ]),
    getGroups () {
      Group.getGroups(this.userId)
        .then(items => { this.setGroups(items) })
        .catch(err => {
          console.log(err)
        })
    },
    openEditDialog (group) {
      this.groupEdit = group
      this.setDialogEditGroup(true)
    }
  },
  mounted () {
    this.getGroups()
  }
}
</script>

